import React, { useEffect, useState } from 'react';
import {NavLink} from "react-router-dom";
// import './SideNav.css';
import './../../assets/style.css';

function SideNavPresenter({isAuthorized}) {
    const menus = [
        { name: "실시간 로봇위치", path: "/map" },
        { name: "로봇 호출", path: "/call" },
        { name: "픽업대 상황", path: "/pickup" },
        { name: "기록", path: "/history" },
        { name: "설정", path: "/setting" }

    ];

    return (
        <div id="side-nav">
            <div id="title">
                <img src={require("../../assets/simbol.png").default} alt="hprobot" />
                <div>Helper Robotics</div>
            </div>
            {menus.map((menu, idx) => {
                return (
                    <NavLink to={menu.path} key={idx} className="nav-item" activeClassName="nav-active">
                        {menu.name}
                    </NavLink>
                );
            })}
            {isAuthorized ? 
            
            <NavLink to='/my-page' id="nav-user">
                        <img src={require("../../assets/simbol.png").default} alt="logo" />

                {/* userid로 user img url 불러오기 */}
                    {sessionStorage.getItem('userName')} 매장
                </NavLink>
                :

            <NavLink to='/login' id="nav-login">
            로그인
        </NavLink>
        
        }
            

        </div>
    );
}

export default SideNavPresenter;
