import React, { Component } from 'react'
import MapRail from './MapRail';
import RobotPosition from './RobotPosition';

export class MapPresenter extends Component {
    constructor(props) {
        super(props)
        this.mapContainer = React.createRef()
        this.state = { unit: 0 };
        this.robotXY = {
            robot1: { x: 11, y: 1 },
            robot2: { x: 10, y: 2 },
            robot3: { x: 11, y: 2 },
            robot4: { x: 1, y: 4 },
            robot5: { x: 1, y: 1 }
        }
    }
    componentDidMount() {
        const size = this.mapContainer.current.offsetWidth;  //mapContainer 컨테이너의 가로길이 측정
        this.setState({unit: size/11});    //MAX X 의 개수로 나눔

        //절대 위치 구하기
        
        // const target = document.getElementById('item-map'); // 요소의 id 값이 target이라 가정
        // const clientRect = target.getBoundingClientRect(); // DomRect 구하기 (각종 좌표값이 들어있는 객체)
        // const relativeTop = clientRect.top; // Viewport의 시작지점을 기준으로한 상대좌표 Y 값.
        // const mapContainer = document.getElementById('item-map');
        
    }


    render() {
        const robotXY = this.robotXY;

        const unit= this.state.unit;
        return (
            <div className="content" id="map">
                <h1 > 실시간 로봇위치</h1>
                <div className="grid-container">
                    <div className="grid-item" id="item-map">
                        실시간 로봇위치 페이지입니다
                        <div ref={this.mapContainer} id="map-container">

                            <RobotPosition robotId="1" position={robotXY.robot1} unit={unit}/>
                            <RobotPosition robotId="2" position={robotXY.robot2} unit={unit}/>
                            <RobotPosition robotId="3" position={robotXY.robot3} unit={unit}/>
                            <RobotPosition robotId="4" position={robotXY.robot4} unit={unit}/>
                            <RobotPosition robotId="5" position={robotXY.robot5} unit={unit}/>
                            <RobotPosition unit={unit}/>
                            <MapRail unit={unit}/>

                            <div className="cross"></div>
                        </div>

                    </div>
                    <div className="grid-item" id="item-status">
                        <h4> status</h4>
                    </div>
                    <div className="grid-item" id="item-live">
                        <h4> 도착 지점 목록</h4>
                    </div>
                </div>
            </div>
        )
    }
}

export default MapPresenter
