import axios from 'axios';
axios.defaults.baseURL = 'http://15.165.182.103:8080/server/src/public';    //15.165.182.103
// axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

export async function getBackRobot(props) {    //시뮬레이션용. 도착 후 서버에 도착신호를 보낸다.
    // users : user id로 map id user node얻어오기
    // robots : map id로부터 쉬고있는 robot 중에 가장 좋은거 선택
    // maps: map name도 갖고오기
    // console.log("toggle "+props)
    const mapName = props.calledMapName;
    const robotId = props.calledRobot;
    const mapId = props.mapId;
    const response = await axios.get('/controller/button/' + robotId + '/' + mapId);
    console.log(response)
    let error = response.status;
    console.log("back error : "+error);

    if (error == 200){  // error 안났을때, 200

        let robotHome = response.data.robot_home;
        let buttonError = postButton([robotHome, mapName, robotId]);
        return [buttonError, robotHome]
        
    }
    else {      //error일때, 즉 response.state가 201 또는 400일때
        alert("로봇이 이동중입니다. 도착 후 버튼을 클릭하세요.");
        return [error, response.data.message];
    }

}
export async function getCallRobot(userId) {    //시뮬레이션용. 도착 후 서버에 도착신호를 보낸다.

    // users : user id로 map id user node얻어오기
    // robots : map id로부터 쉬고있는 robot 중에 가장 좋은거 선택
    // maps: map name도 갖고오기
    console.log("call : /controller/button/" + userId);

//방법1
    try{
        const response = await  axios.get('/controller/button/' + userId);
        let error = response.data.error;
        console.log("call error : "+error);
    
        if (error){ //error일때, 즉 response.state가 201 또는 400일때
            alert(response.data.message);
            return [error, 0, response.data.message]
        }
        else {  // error 안났을때, 200
            let robotId = response.data.robot_id;
            let userNode = response.data.user_node;
            let mapName = response.data.map_name;
            let buttonError = postButton([userNode, mapName, robotId]);
            return [buttonError, robotId, mapName];
        }
    } catch (e) {
        alert("호출 가능한 로봇이 없습니다. 기다려주세요.\n"+e.message);
            return [true, 0, e.message]
    }


}
function postButton(params) {
    console.log("param ",params);

    const response = axios({
        method: 'post',
        url : '/mqtt/button',
        data : {
            final_node : params[0],
            map_name : params[1],
            robot_id : params[2]
        }
    });
    console.log("button "+response);
    // let error = response.data.error
    // console.log("button "+error)
    return false;
}


