import React, { Component } from 'react';
import CallStatus from './CallStatus';
import {getBackRobot, getCallRobot} from './CallController';
export class CallPresenter extends Component {
    constructor(props) {
        super(props)
        // const userId = sessionStorage.getItem('userId');
        // const mapId = sessionStorage.getItem('mapId');
        // const userName = sessionStorage.getItem('userName');
        // console.log(userId);
        this.state = {
            isCall: false,
            calledRobot: 0,
            mapName : "",
            count: 2
        };
        // this.toggle = this.toggle.bind(this);

    }
    toggle = async ( userId, mapId, userName) => {
        const isCall = this.state.isCall;
        const count = this.state.count;
        const calledRobot = this.state.calledRobot;
        const calledMapName = this.state.mapName;

        const change = isCall ? 1 : -1;     //실제 로봇수 아님, 임시 테스트용

        if (isCall) {   // 픽업대 버튼 눌렀을 때
            let [error, message] = await getBackRobot({mapId, calledRobot, calledMapName});
            console.log("error ? back ", message);

            if (!error){
                this.setState(prevState =>({
                    ...prevState,
                    isCall: !isCall,
                    count: count + change     //실제 로봇수 아님, 임시 테스트용
                }))
            }
        }
        else {   // 호출 버튼 눌렀을 때
            let [error, robotId, mapName] = await getCallRobot(userId);
            console.log("error ? call ", error);
            if (!error){
                this.setState(prevState =>({
                    ...prevState,
                    isCall: !isCall,
                    count: count + change,
                    calledRobot :  robotId,
                    mapName: mapName
                }))
            }
        }
       
        // const robotList = await GetRobotStatus(mapId);   변수 처리 공부해서 다시 수정
        // console.log("toggle "+mapId+" : "+robotList[0])
        


    }
    countRobot = () => {
        
    }
    render() {
        const userId = sessionStorage.getItem('userId');
        const mapId = sessionStorage.getItem('mapId');
        const userName = sessionStorage.getItem('userName');
        const isCall = this.state.isCall;
        const count = this.state.count;
        const robotStatus = this.state.robotStatus;


        return (
            <div className="content" id="call">
                <h1 > 로봇 호출</h1>
                <div className="grid-container">
                    <div className="grid-item" id= "item-button">
                        <h4>로봇 호출 버튼</h4>
                        <div id="calling-map-name">
                            {userId}의 매장 입니다.
                        </div>
                        <div id="called-robot">
                        {isCall? 
                        <div><span>{this.state.calledRobot}</span> 번 로봇 배정 {this.state.mapName}</div>  //state에서 직접 불러옴. render위에서 부르면 왜인지 object뜸
                        :
                        <div> </div>
                        }
                            
                        </div>
                        <button id="call-button" onClick={this.toggle.bind(this, userId, mapId, userName)}>
                            {isCall? "픽업대" : "호출"}
                            </button>
                    </div>
                    <div className ="item-call-status">
                        <CallStatus/>

                    </div>
            
                </div>
            </div>
        )
    }
}

export default CallPresenter
